import User from "@jx3box/jx3box-common/js/user.js";
import { getMeridiansList, createMeridians, updateMeridians, getMeridians, removeMeridians } from '@/service/meridians'

export default {
    name: "Meridians",
    data: function () {
        return {
            tempId: 0,
            dialogVisible: false,
            zhumai: [
                {
                    name: "",
                    id: "60",
                    class: "renmai",
                    requireSuccess: true,
                },
                { name: "", id: "91", class: "dumai" },
                { name: "", id: "118", class: "daimai" },
                { name: "", id: "156", class: "chongmai" },
            ],
            mai: [
                {
                    name: "",
                    content: "",
                    title: "",
                    class: "idx-yutang idx-ap1",
                    type: 1,
                    state: 0,
                },
                {
                    name: "",
                    content: "",
                    title: "",
                    class: "idx-qugu idx-ap2",
                    type: 2,
                    state: 0,
                },
                {
                    name: "",
                    content: "",
